body {
  font-family: Arial, sans-serif;
  margin: 0;
  padding: 0;
  width: 300px;
}

.App {
  padding: 10px;
}

textarea, select, button {
  width: 100%;
  margin: 5px 0;
}

textarea {
  height: 100px;
}

button {
  padding: 10px;
  background-color: #4CAF50;
  color: white;
  border: none;
  cursor: pointer;
}

button:hover {
  background-color: #45a049;
}
